import React from 'react';

import { PrjTopCmp, PrjInfoCmp, PrjBottomCmp } from '../component';

import '../css/project.scss';

import projectData from './projectData.json';

// image import
import imgVisua1 from '../images/work/ourwork-aritaum-img@2x.jpg';
import prjImg1 from '../images/work/prj_img1.jpg';

class Project1 extends React.Component {
    constructor(props){
        super(props);

        // json 파일에서 가져올 리스트 번호
        this.idx = 1;
        this.state = {
            type: projectData[this.idx].type,        //project type
            cate: projectData[this.idx].cate,        //sorting category / 카테고리의 클래스를 받아 sorting
            title: projectData[this.idx].title,      //title
            desc: projectData[this.idx].desc,        //description
            mission: projectData[this.idx].mission,  //mission
            partner: projectData[this.idx].partner,  //partner
            launch: projectData[this.idx].launch,    //launch
            url: projectData[this.idx].url,          //url
            img:imgVisua1,                           //visual image
        }
    }

    render(){
        return (
            <React.Fragment>

                <div className="cms">
                    <PrjTopCmp
                        type={this.state.type} 
                        title={this.state.title} 
                        desc={this.state.desc} 
                    />

                    <div className="inner_left">
                        <div className="visual">
                            <img src={this.state.img}/>
                        </div>
                    </div>

                    <div className="content inner_left">
                            
                        <div className="view_coltype">
                            <div className="left">
                                <p className="tit">challenge</p>
                            </div>
                            <div className="right">
                                <p className="desc">
                                    뉴스룸은 심플(simple 복잡하지 않고 쉬운)과 Clarifed(명확한)를 콘셉트로한 현대카드ㆍ현대캐피탈의 공식 홍보채널로 
                                    급변하는 디지털 환경에 적극적으로 대응하고 미디어에 정확하고 깊이있는 정보들을 빠르게 제공하기 위한 
                                    블로그 형태의 온라인 플랫폼입니다. 현대카드의 Tone &amp; Manner를 유지하여 일관된 브랜드 경험을 느끼게하고, 
                                    콘텐츠에만 집중할 수 있는 UX/UI로 현대카드ㆍ현대캐피탈이 미디어와 원활한 커뮤니케이션을 할 수있는 긍정적인 
                                    공간으로 느껴지게 하는데 초점을 맞추었습니다.
                                </p>
                                
                                <PrjInfoCmp 
                                    mission={this.state.mission}
                                    partner={this.state.partner}
                                    launch={this.state.launch}
                                    url={this.state.url}
                                />
                            </div>
                        </div>
                        
                        {/* PrjInfoCmp 컴포넌트로 변경전 */}
                        {/* <ul className="prinfo">
                            <li>
                                <p className="cate">Mission</p>
                                <p className="cont">
                                    {this.state.mission}
                                </p>
                            </li>
                            <li>
                                <p className="cate">Partner</p>
                                <p className="cont">
                                    {this.state.partner}
                                </p>
                            </li>
                            <li>
                                <p className="cate">launch</p>
                                <p className="cont">
                                    {this.state.launch}
                                </p>
                            </li>
                            <li>
                                <p className="cate">Link</p>
                                <p className="cont">
                                    {this.state.url}
                                </p>
                            </li>
                        </ul> */}

                    </div>

                    
                    <div className="thumb_set">
                        <img src={prjImg1} alt=""/>
                        <p>
                            상세 컨텐츠<br/>
                            상세 컨텐츠<br/>
                            상세 컨텐츠<br/>
                            상세 컨텐츠<br/>
                            상세 컨텐츠<br/>
                            상세 컨텐츠<br/>
                            상세 컨텐츠<br/>
                            상세 컨텐츠<br/>
                            상세 컨텐츠<br/>
                            상세 컨텐츠<br/>
                        </p>
                    </div>   

                    <PrjBottomCmp/>

                </div>

            </React.Fragment>
        )
    }
}

export default Project1;