import React from 'react';
import AOS from 'aos';
import 'aos/dist/aos.css';

import { PrjTopCmp, PrjInfoCmp, PrjBottomCmp } from '../component';

import '../css/project.scss';

// project style import
// import '../css/asiana.scss';

import projectData from './projectData.json';


class AsianaMo extends React.Component {
    constructor(props){
        super(props);

        this.idx = 2;

        this.state = {
            type: projectData[this.idx].type,        
            cate: projectData[this.idx].cate,        
            title: projectData[this.idx].title,      
            desc: projectData[this.idx].desc,        
        }
    }

    componentDidMount(){
        AOS.init({});
    }

    render(){
        return (
            <React.Fragment>

                View - Mobile


            <PrjBottomCmp/>

            </React.Fragment>
        )
    }
}

export default AsianaMo;