import React from 'react';
import MobileCheck from '../js/mobileCheck.js';

import BeautyPointPc from './BeautyPointPc';
import BeautyPointMo from './BeautyPointMo';

class BeautyPoint extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            isMobile : false
        }
    }

    componentDidMount(){
        MobileCheck(this);
    }

    render(){
        return (
            <React.Fragment>
                {this.state.isMobile ? <BeautyPointMo /> : <BeautyPointPc />}
            </React.Fragment>
        )
    }
}

export default BeautyPoint;