/****************************/
/* MobileCheck              */
/****************************/

export default function MobileCheck(prj){

    // 1. initial check
    let winWid = window.innerWidth;
    if(prj.state.isMobile){
        if(winWid > 480){
            prj.setState({
                isMobile: false
            });
        }
    } else {
        if(winWid <= 480){
            prj.setState({
                isMobile: true
            });
        }
    }

    // 2. resize check 
    let _this = prj; 
    window.addEventListener('resize', function(){
        let winWid = window.innerWidth;

        if(_this.state.isMobile){
            if(winWid > 480){
                _this.setState({
                    isMobile: false
                });
            }
        } else {
            if(winWid <= 480){
                _this.setState({
                    isMobile: true
                });
            }
        }
    });
}