import React from 'react';

import { PrjTopCmp, PrjInfoCmp, PrjBottomCmp } from '../component';


import '../css/project.scss';

import projectData from './projectData.json';

// image import
import imgVisua1 from '../images/work/ourwork-lottehotel-img@2x.jpg';

class Project3 extends React.Component {
    constructor(props){
        super(props);

        this.idx = 3;

        this.state = {
            type: projectData[this.idx].type,
            cate: projectData[this.idx].cate,
            title: projectData[this.idx].title,
            desc: projectData[this.idx].desc,        
            mission: projectData[this.idx].mission,  
            partner: projectData[this.idx].partner,  
            launch: projectData[this.idx].launch,    
            url: projectData[this.idx].url,          
            img:imgVisua1,                       
        }

    }

    render(){
        return (
            <React.Fragment>

                <div className="cms">

                    <PrjTopCmp
                        type={this.state.type} 
                        title={this.state.title} 
                        desc={this.state.desc} 
                    />

                    <div className="inner_left">
                        <div className="visual">
                            <img src={this.state.img}/>
                        </div>
                    </div>

                    <div className="content inner_left">

                        <PrjInfoCmp 
                            mission={this.state.mission}
                            partner={this.state.partner}
                            launch={this.state.launch}
                            url={this.state.url}
                        />

                        상세 컨텐츠<br/>
                        상세 컨텐츠<br/>
                        상세 컨텐츠<br/>
                        상세 컨텐츠<br/>
                        상세 컨텐츠<br/>
                        상세 컨텐츠<br/>
                        상세 컨텐츠<br/>
                        상세 컨텐츠<br/>
                        상세 컨텐츠<br/>
                        상세 컨텐츠<br/>
                    </div>

                    <PrjBottomCmp/>
                </div>

            </React.Fragment>
        )
    }
}

export default Project3;